<template>
  <v-timeline>
    <v-timeline-item
      v-for="(job, i) in jobs"
      :key="i"
      small
      right
    >
      <template v-slot:opposite>
        <span
          :class="`font-weight-bold`"
          v-text="job.execution.startTime"
        ></span> ~
        <span
          :class="`font-weight-bold`"
          v-text="job.execution.endTime"
        ></span>
        <div :class="`font-weight-bold`" v-show="job.elapsed != '0'">
          {{ job.elapsed | elapsedmask}}  
        </div>
      </template>
      <div class="py-4">
        <v-row 
          v-for="step in job.stepList"
          :key="step.id">
          <v-col>
            <v-card :style="step.status !='COMPLETED' ? 'border: 2px solid red;' : 'border: 1px solid white;'" max-width=350>
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title>{{step.name}} ({{step.exitCode}} )</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
              <v-card-text> 
                {{step.stime}} ~ {{step.etime}} {{step.elapsed | elapsedmask}}
              </v-card-text>
            </v-card>
          </v-col>
        </v-row>
      </div>
    </v-timeline-item>
  </v-timeline>
</template>
<script>
import axios from "axios"
import { common } from "../func/common"

export default {
  data () {
    return {
      jobs: [],
      show: false,
      instanceId: this.$route.params.instanceId,
    }
  },
  created() {
    this.findHistotyByInstanceId(this.instanceId)
  },
  filters: {
    elapsedmask: function(value) {
      return common.getElapsedFormat(value)
    }
  },
  methods: {
    findHistotyByInstanceId(instanceId) {
      axios.get(`/job/history/${instanceId}`)
        .then(res => { 
          this.jobs = res.data
        })
        .catch(err => {
          console.error(err)
        })
    },
  }
}
</script>